import { useState } from 'react'
import { Link } from 'react-router-dom'
import { Form, Button } from 'react-bootstrap'
import LoadingSpinner from '../../../UI/LoadingSpinner/LoadingSpinner'


const NewsCreate = ({ submitHandler, loadingCreate, errorCreate }) => {

  const [title, setTitle] = useState('')
  const [subtitle, setSubtitle] = useState('')
  const [description, setDescription] = useState('')
  const [picture, setPicture] = useState([])

  return (
    <>
      <Link className='admin__link-text' to='/admin/aktualnosci'>
        Powrt
      </Link>
      <h1 className='admin__title admin__title--edit-user'>Tworzenie artykuu</h1>
      {loadingCreate && <LoadingSpinner />}
      {errorCreate && <p>Bd: {errorCreate}</p>}
      <Form className='admin__form' onSubmit={(e) => submitHandler(e, title, subtitle, description, picture)}>
        <Form.Group controlId='title'>
          <Form.Label className='admin__form-label'>Tytu</Form.Label>
          <Form.Control
            className='admin__search-input admin__search-input--short'
            type='text'
            placeholder='Tytu'
            value={title}
            onChange={(e) => setTitle(e.target.value)}
          >
          </Form.Control>
        </Form.Group>
        <Form.Group controlId='subtitle'>
          <Form.Label className='admin__form-label'>Podtytu</Form.Label>
          <Form.Control
            className='admin__search-input admin__search-input--short'
            type='text'
            placeholder='Podtytu'
            value={subtitle}
            onChange={(e) => setSubtitle(e.target.value)}
          >
          </Form.Control>
        </Form.Group>

        <Form.Group controlId='description'>
          <Form.Label className='admin__form-label'>Opis</Form.Label>
          <Form.Control
            // className='admin__search-input admin__search-input--short'
            as="textarea"
            placeholder='Opis...'
            value={description}
            onChange={(e) => setDescription(e.target.value)}
          >
          </Form.Control>
        </Form.Group>

        <Form.Group controlId='image'>
          <Form.Label className='admin__form-label'>Zdjcie</Form.Label>
          <Form.Control
            type="file"
            label='Wybierz plik'
            onChange={(e) => setPicture([e.target.files[0]])}
          >

          </Form.Control>

        </Form.Group>

        <Button className='admin__button' type='submit' variant='primary'>
          Stwrz
        </Button>
      </Form>

    </>
  )
}

export default NewsCreate;